import React from 'react'
import "./watch.scss"

import { Link, useLocation } from 'react-router-dom';

const Watch = () => {

  const location = useLocation();
  const movieVideo = location.movieVideo;

  return (
    <div className='watch'>
        <div className="back">
            <Link to='/'>
                Home
            </Link>
        </div>
        <iframe className='movie' src={movieVideo} title="movie" frameborder="0" marginwidth="0" marginheight="0" scrolling="no" allowFullScreen allow="fullscreen;"></iframe>
    </div>
  )
}

export default Watch